<template>
    <form @submit.prevent="AddIngreso" >
              <div>
                               
                   <input
                      type='date'
                      name='fecha'
                      class='form-control mt-3'
                      placeholder='Fecha'
                      autoComplete='off'
                      v-model="ingreso.fecha"
                  /> 
              
                  
                  <input
                      type='text'
                      name='descripcion'
                      class='form-control mt-3'
                      placeholder='Descripcion'
                      autoComplete='off'
                      v-model="ingreso.descripcion"
  
                  />
                   <input
                      type='number'
                      name='monto'
                      class='form-control mt-3'
                      placeholder='Monto'
                      autoComplete='off'
                      v-model="ingreso.monto"
  
                  />
                  
  
                  <button 
                  class='btn btn-primary form-control  mt-2'
                  type='submit'  
                  :disabled="getIsLoading"
                  >
                  Guardar
  
                  </button>
  
              </div>
          </form>
  </template>
  
  <script>
  import { mapActions, mapGetters } from 'vuex'
  
  export default {
      data() {
          return{
              ingreso:{}
          }
      },
      computed:{
          ...mapGetters('ferneyIngreso',['getIsLoading'])
      },
      methods:{
          ...mapActions('ferneyIngreso',['addIngreso']),
          async AddIngreso(){
              var ingresoAdd = {
                  fecha: this.ingreso.fecha,
                  descripcion: this.ingreso.descripcion,
                  monto: this.ingreso.monto
              }
              await this.addIngreso(ingresoAdd)
          }
      },
  }
  </script>
  
  <style>
  
  </style>